@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
  --primary-color: #2096f3;
  --regal-blue: #034378;
  --san-juan: #2d4e68;
  --bermuda: #77d7b9;
  --white: #fff;
  --black: #000;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  background: url(/static/media/main-background.f46aa65e.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}







/*
====================================================
  BRAND CSS
====================================================
*/

.inputBrand {
  margin: 0 auto;
  width: 400px;
}

.btnSubmitBrand {
  margin: 0 353px;
  width: 400px;
}

/*
====================================================
  CATEGORY 
====================================================
*/

#formAddCategory {
  width: 300px;
  margin: 0 auto;
}




/*
====================================================
  BREADCRUMP
====================================================
*/

.breadcrump {
  list-style: none;
  display: flex;
  align-items: center;
}

.breadcrumb-separator {
  color: #333;
  margin: auto 6px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.breadcrumb-collapser {
  display: flex;
  align-items: center;
}

.breadcrumb-collapser svg {
  cursor: pointer;
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  color: #000;
  width: 25px;
  height: 25px;
}

.breadcrumb-collapser svg:hover,
.breadcrumb-collapser svg:focus {
  color: #999;
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
}

.breadcrumb-collapser svg:active {
  color: #333;
}




/* -------------------------------DETAIL PRODUCT-------------------- */

/*
====================================================
  MAIN
====================================================
*/

.main {
   height: 100%;
   width: 100%;
   margin-left: 2.2rem;
   margin-top: 1rem;
}

.main__card {
   max-width: 345px;
}
.main__card img {
   width: 100%;
   height: 320px;
}
.card__content {
   display: none;
   -webkit-transition: all .5s ease-in;
   transition: all .5s ease-in;
   position: absolute;
   bottom: 0;
   right: 0;
   width: 100%;
   background: black;
   color:white;
}
.main__card:hover .card__content {
   display: block;
}

.navbar {
   position: relative;
   top: 0;
   display: flex;
   background-color: var(--primary-color);
   justify-content: flex-end;  
   padding: 1rem;
   opacity: .8;
   border-bottom: 2px solid #eee;
}
.navbar h1 {
   cursor: pointer;
   text-transform: uppercase; /* Chrome 10-25, Safari 5.1-6 */
   background: -webkit-gradient(linear, left top, right top, from(#0575e6), to(#021b79));
   background: linear-gradient(to right, #0575e6, #021b79);
   -webkit-background-clip: text;
           background-clip: text;
   margin-right: 20em;
   -webkit-text-fill-color: transparent;
}
.navbar button {
   margin-right: 80px;
   color: #fff;
}

/*
====================================================
  AUTHENTICATION 
====================================================
*/

.authentication {
   position: absolute;
   height: 100%;
   width: 100%;
   background-position: center;
   background-repeat: no-repeat;
   background-size: cover;
 }
 
 .loginform {
   width: 405px;
   max-height: 300px;
   margin: 160px auto;
 }
 
 .loginform .input {
   margin: 25px 20px;
 }
 
 .loginform button {
   width: 200px;
   padding: 15px;
   margin: 5px 120px 20px;
 }
.category__container {
   display: flex;
   flex-direction: row;
   padding: 1rem;
   align-items: center;
   justify-content: center;
}
.category__container button{
   margin-left: 1rem;
}

/*
====================================================
  FOOTER
====================================================
*/

.footer {
   position: relative;
   bottom: 0;
   display: flex;
   opacity: 0.8;
   justify-content: center;
   align-items: center;
   padding:12px;
   background: var(--primary-color);
   color: white;
   font-size: 13px;
 }
 .footer img{
   width: 40px;
   height: 40px;
   border-radius: 10px;
   margin-left: 10px;
 }
.brand__container{
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: center;
   padding:1rem
}
.brand__container button{
   margin-left: 1rem;
}
.sidelist {
   background: #fff;
   height: 100vh;
   border-right: 2px solid #eee;
}

/*
====================================================
  PRODUCT 
====================================================
*/

.upload__image-wrapper {
   display: flex;
   flex-direction: column;
   margin: 10px 20px;
}

.image-item {
   display: flex;
   flex-direction: column;
   justify-content: flex-start;
   align-items: center;
}

.upload__image-wrapper div button {
   margin-left: 2rem;
}
.notfoundpage {
   display: flex;
   height: 80%;
   justify-content: center;
   align-items: center;
}
.notfoundpage__container {
   text-align: center;
   padding: 30px 45px;
   border-radius: 5px;
   color: #eee; /* Chrome 10-25, Safari 5.1-6 */
   background: -webkit-gradient(
      linear,
      right top, left top,
      from(#4286f4),
      to(#373b44)
   );
   background: linear-gradient(
      to left,
      #4286f4,
      #373b44
   ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.notfoundpage__container h1{
   margin: 1rem 0;
}
.notfoundpage__container a{
   font-size: 1.2rem;
}

.project__posts {
   padding: 1rem 0;
}
.project__filterbar{
   width: 100%;
   background-color: #eee;
   display: flex;
   align-items: center;
   justify-content: center;
   padding: 1rem 0;
   box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

